.widgets {
    position: sticky;
    top: 80px;
    flex: 0.2;
    /* background-color: white; */
    background-color: #1B1F23;
    border-radius: 10px;
    border: 1px solid lightgray;
    height: fit-content;
    padding-bottom: 10px;
}

.widgets__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    /* optional */
    color: gray;
}

.widgets__header > h2 {
    font-size: 16px;
    font-weight: 600;
    /* optional */
    color: gray;
}

.widgets__article {
    display: flex;
    padding: 10px;
    cursor: pointer;
}

.widgets__article:hover {
    /* background-color: whitesmoke; */
    background-color: darkslategray;
}

.widgets__articleLeft {
    color: #0177b7;
    margin-right: 5px;
}

.widgets__articleLeft > .MuiSvgIcon-root {
    font-size: 15px;
    /* optional */
    color: gray;
}

.widgets__articleRight {
    flex: 1;
}

.widgets__articleRight > h4 {
    font-size: 14px;
    /* optional */
    color: gray;
}

.widgets__articleRight > p {
    font-size: 12px;
    color: gray;
}
